.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

.cell {
    width: 7vmin;
    height: 7vmin;
    border: 1px solid black;
}

input {
    font-size: 190%;
    text-align: center;
}

body {
    margin: 20px;
}

#submitButton {
    margin-top: 20px;
    height: 30px;
    width: 75px;
    font-size: 20px;
}

.row {
    margin: 0px;
}

@media screen and (max-width: 480px) {
    .row {
        height: 6.7vmin;
    }

    .cell {
        width: 5.5vmin;
        height: 5.5vmin;
        border: 0.5px solid black;
    }

    input {
        font-size: 100%;
        text-align: center;
    }

    body {
        margin: 20px;
    }

    #submitButton {
        margin-top: 20px;
        height: 30px;
        width: 75px;
        font-size: 20px;
    }
}